<template>
  <div id="app">
    <h3>
      请输入github仓库链接或者gitee公开仓库，实例：https://github.com/webpon/git-demo.git,
      默认访问inde.html，否则按照目录形式访问
    </h3>
    <t-input v-model="gitLink"></t-input>
    <h3>请输入项目名：比如，test, 这样你就http://39.103.233.82/test访问你的网站了</h3>
    <t-input v-model="projectName"></t-input>
    <t-button @click="submit">提交</t-button>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      gitLink: "",
      projectName: "",
    };
  },
  methods: {
    get(url, data, callback, type) {
      if (url === "undefined") return;
      if (data) {
        var mySearchParams = new URLSearchParams();
        for (const key in data) {
          mySearchParams.append(key, data[key]);
        }
        console.log(mySearchParams.toString());
        var urlArr = url.split("?");
        console.log(urlArr);
        if (urlArr[1] === "" || urlArr[1] === undefined) {
          url = urlArr[0] + "?" + mySearchParams.toString();
        } else {
          url += "&" + mySearchParams.toString();
        }
      }
      console.log(url);
      const req = new XMLHttpRequest();
      req.responseType = type ? type : "json";
      req.open("GET", url);
      req.send();
      if (callback === undefined) {
        return new Promise((resolve, reject) => {
          // 处理结果
          req.onload = function () {
            if (req.readyState === 4) {
              if (req.status >= 200 && req.status < 300) {
                resolve(req.response);
              } else {
                reject(req.status);
              }
            }
          };
        });
      } else {
        req.onload = function () {
          callback(req.response);
        };
      }
      return this;
    },
    submit() {
      this.get("http://39.103.233.82:9000/ajax", {
        test: this.gitLink,
        name: this.projectName,
      });
      setTimeout(() => {
        this.$message.info({
          content: "部署成功了，请访问：http://39.103.233.82/" + this.projectName,
          duration: 10000,
        });
      }, 1000);
    },
  },
};
</script>

